import '../styles/globals.css';
import Layout from '../components/Layout';
import { ThemeProvider } from '../context/ThemeContext';
import { Inter, Merriweather } from 'next/font/google';

// Configuração da fonte para o corpo do texto
const inter = Inter({
    subsets: ['latin'],
    display: 'swap',
    variable: '--font-family-body', // Define a variável CSS
});

// Configuração da fonte para os títulos
const merriweather = Merriweather({
    subsets: ['latin'],
    weight: ['300', '400', '700', '900'],
    style: ['normal', 'italic'],
    display: 'swap',
    variable: '--font-family-headings', // Define a variável CSS
});

function MyApp({ Component, pageProps }) {
    return (
        <ThemeProvider>
            <Layout>
                <div className={`${inter.variable} ${merriweather.variable}`}>
                    <Component {...pageProps} />
                </div>
            </Layout>
        </ThemeProvider>
    );
}

export default MyApp;